import { getDefaultNormalizer } from '@testing-library/react';
import { useEffect, useState } from 'react';
import Cards from "../components/Cards"

import filter from "../images/filter.png";
import {HashLoader} from "react-spinners"



function Home () {

const [data , setData] = useState({});
const [loading , setLoading] = useState(false);
const [search , setSearch] = useState('top');



const getData = async (s)=>{
  await fetch(`https://www.reddit.com/r/SpotifyPlaylists/${s}.json`)
  .then(res =>  res.json()) 
  .then(data => {
      setData(data)
      setLoading(true);
    })
}

useEffect(()=>{
  getData(search)
  console.log(data);  
},[])

useEffect(()=>{
setLoading(false);
getData(search);
},[search])

const handleChange = (e)=>{
  const {value} = e.target;
  setSearch(value);
}

  return (
    <div className="App h-screen p-10 ">
    <div className='text-white text-center bg-black w-full'>
      <label className=' flex justify-center'>

      <img src={filter} alt="sd"/> 
      <select className='bg-black text-white h-auto  px-2 ' onChange={handleChange}>
        <option value="top">top💯</option>
        <option value="rising">rising📈</option>
        <option value="new">new🔥</option>
      </select>
      </label>

    </div>

    {
      loading ?
      <>
{
  data.data.children.map((data, id) => { 
      console.log('====================================');
      console.log(data);
      console.log('====================================');
    return(

    <>
        <Cards id = {id}
         url = {data.data.url}
         title = {data.data.title}
         link_flair_text = {data.data.link_flair_text}
          />
    </>



 

    )
  })
}

    
      </>
    
     :
     <div className='mt-[50%]  flex justify-center'>
     <HashLoader
  color="#1f8326"
  speedMultiplier={1}
/>
     </div>
    }
    

    </div>
  );
}
export default Home;